<template>
    <div class="mfa-education">
        <h1 class="title">{{ title }}</h1>
        <b-container class="py-3 py-lg-5">
            <b-img
                class="border-radius1 shadow1 mb-3 mb-lg-5"
                fluid
                src="/img/MfaMalAnders_AusbildungBerufsbild_1200.jpg"
                alt="MFA Ausbildung und Berufsbild"
            />
            <p>
                Die Medizinische Fachangestellte, kurz gesagt MFA oder in der
                frheren Berufsbezeichnung bis ins Jahr 2006 ArzthelferIn
                genannt, ist ein Multitalent mit vielen Aufgaben und Talenten.
            </p>
            <p>
                Da jeder ab und zu eine Arztpraxis besucht, kann man sich
                vorstellen, wie die typischen Ttigkeiten einer MFA aussehen.
                Was zu diesem wundervollen und abwechslungsreichen Beruf aber
                eigentlich alles dazu gehrt und wie die Ausbildung zur
                Medizinischen Fachangestellten aussieht, beschreiben wir hier.
            </p>
            <h2>
                Welche Aufgaben erwarten mich als Medizinische Fachangestellte?
            </h2>
            <p>
                Von manchen Patienten als Sprechstundenhilfe betitelt, ist
                eine Medizinische Fachangestellte keineswegs nur eine HelferIn
                fr die Sprechstunde. MFAs organisieren die Arztpraxis und
                alles, was mit der Patientenversorgung und zu einer
                erfolgreichen Praxisfhrung dazu gehrt.
            </p>
            <p>
                Je nachdem, wie eine Praxis organisiert ist sowie Praxisgre
                und Fachrichtung, werden MFAs als Allrounder eingesetzt und
                erledigen quasi alle anfallenden Aufgaben in der Praxis. Neben
                der Sprechstundenorganisation und Patientenbetreuung, gibt es
                zahlreiche Ttigkeiten fr eine MFA, die fr den Patienten nicht
                ersichtlich sind. Der Beruf vereint brokratische,
                organisatorische und medizinische Aufgaben.
            </p>
            <b-row cols="12" lg="4">
                <b-col>
                    <div class="example">
                        <h3 class="h5">
                            Beispiele fr medizinische Ttigkeiten:
                        </h3>
                        <p class="mb-0">
                            Sie assistieren den rzten bei Untersuchungen oder
                            chirurgischen Eingriffen und bereiten
                            Untersuchungsrume entsprechend vor und nach. Sie
                            legen Verbnde an. Manche MFAs arbeiten im
                            praxiseigenen Labor, nehmen Blut ab und fhren
                            teilweise selber Blut- und Urinuntersuchungen durch.
                            Sie verabreichen Injektionen, Impfungen und
                            Infusionen. Nicht zuletzt ist die MFA auch fr die
                            fachgerechte Durchfhrung der Desinfektion von
                            Arbeitsflchen und Instrumenten zustndig.
                        </p>
                    </div>
                </b-col>
                <b-col>
                    <div class="example">
                        <h3 class="h5">
                            Beispiele fr brokratische Ttigkeiten:
                        </h3>
                        <p class="mb-0">
                            Von Ttigkeiten an der Rezeption, Rezepte erstellen,
                            Antrge fr Krankenkassen oder Versorgungskassen
                            ausfllen, Bestellungen und den Einkauf der Praxis
                            zu managen, erledigen Medizinische Fachangestellte
                            ebenfalls die Abrechnung und etablieren ein
                            funktionierendes Qualittsmanagement.
                        </p>
                    </div>
                </b-col>
                <b-col>
                    <div class="example">
                        <h3 class="h5">
                            Beispiele fr organisatorische Ttigkeiten:
                        </h3>
                        <p class="mb-0">
                            Medizinische Fachangestellte sorgen fr einen
                            reibungslosen Sprechstundenablauf. Sie vereinbaren
                            Termine so, dass Patienten nicht zu lange warten.
                            Koordinieren Untersuchungen, sodass entsprechende
                            Rume und Kollegen zur Verfgung stehen. Notflle am
                            Telefon oder an der Rezeption erkennen sie schnell
                            und knnen fachgerecht handeln.
                        </p>
                    </div>
                </b-col>
            </b-row>
            <h2>
                Welche Fhigkeiten muss ich als Medizinische Fachangestellte
                haben?
            </h2>
            <p>
                Als Medizinische Fachangestellte muss man nicht perfekt sein,
                aber man ist schon ziemlich nah dran . Um den Praxisalltag mit
                all seinen Herausforderungen meistern zu knnen und diesen
                wirklich wunderbaren Beruf mit Freude auszuben, helfen
                bestimmte Eigenschaften:
            </p>
            <b-row cols="1" cols-lg="2">
                <b-col>
                    <ul>
                        <li>Organisationstalent</li>
                        <li>Gutes Zeitmanagement</li>
                        <li>Multitaskingfhig</li>
                        <li>Freude am Umgang mit Menschen</li>
                        <li>Gute Kommunikationsfhigkeit</li>
                        <li>Einfhlungsvermgen</li>
                        <li>
                            Verschwiegenheit (Patientengeheimnis und
                            Schweigepflicht)
                        </li>
                        <li>Interesse an der Medizin</li>
                        <li>
                            Keine Scheu vor dem menschlichen Krper, seinen
                            Sekreten bzw. Ausschedungen und Gerchen
                        </li>
                        <li>Gewissenhaftes arbeiten</li>
                        <li>Verantwortungsbewusstsein</li>
                        <li>Genauigkeit</li>
                        <li>
                            Eine gewisse Serviceorientiertheit (der Patient ist
                            schlielich Knig )
                        </li>
                    </ul>
                </b-col>
                <b-col class="d-flex justify-content-center align-items-center">
                    <b-img
                        style="max-width: 80%"
                        class="my-4"
                        fluid
                        src="~@/assets/img/profile_mfa.svg"
                    />
                </b-col>
            </b-row>
            <h2>
                Wie luft die Ausbildung zur Medizinischen Fachangestellten ab?
            </h2>
            <p>
                Die Ausbildung zur MFA findet berufsbegleitend statt. Das heit,
                praktische Kenntnisse werden in der Ausbildungspraxis vermittelt
                und theoretisches Wissen erlernst du in der Berufsschule.
            </p>
            <p>
                Zur Berufsschule gehst du, je nach Bundesland, 1-2 mal die
                Woche. In der Berufsschule lernst du alles, was du im
                Berufsalltag bentigst. Dazu zhlen medizinische Inhalte, wie
                die Lehre der Anatomie, Pathologie und Physiologie, um den
                Krper, seine normalen Funktionen und Krankheiten zu verstehen.
                Daneben behandelt ihr verwaltungstechnischen Lehrstoff ber
                Abrechnung, Datenschutz und das deutsche Gesundheitssystem. Aber
                auch interessante Dinge zu Hygienestandards, Kommunikation mit
                Patienten und Qualittsmanagement werden in der Berufsschule
                erlernt.
            </p>
            <p>
                Insgesamt dauert die Ausbildung zur MFA drei Jahre.
            </p>
            <p>
                Die Inhalte der Ausbildung sind in einem sogenannten
                Ausbildungsrahmenplan in der
                <b-link
                    href="https://www.bundesaerztekammer.de/fileadmin/user_upload/downloads/MFA_Verordnung_05052006.pdf"
                    target="_blank"
                    >Verordnung ber die Berufsausbildung zum Medizinischen
                    Fachangestellten / zur Medizinischen
                    Fachangestellten</b-link
                >
                verankert. Der Rahmenplan legt fest, welche Inhalte in welchem
                Ausbildungsjahr vermittelt werden mssen. Die Ausbildungspraxis
                schreibt einen Ausbildungsplan nieder, an dem sich die Azubis
                orientieren knnen.
            </p>
            <h3>Inhalte der MFA Ausbildung</h3>
            <div class="example">
                <h3 class="h5">
                    1. Ausbildungsjahr
                </h3>
                <p class="mb-0">
                    Im ersten Ausbildungsjahr erlernt man zunchst viele
                    Grundlagen, z.B. zum Arbeitsrecht und Datenschutz. Aber auch
                    den richtigen Umgang mit Patienten und wie du bei
                    Untersuchungen oder Therapien richtig assisierst. Oft ist
                    hier noch ein/e erfahrene/r KollegIn dabei, um dir zu
                    helfen.
                </p>
            </div>
            <div class="example">
                <h3 class="h5">
                    2. Ausbildungsjahr
                </h3>
                <p class="mb-0">
                    Im zweiten Lehrjahr kommen Aufgabenfelder aus dem Bereich
                    des Praxismanagements hinzu, wie Abrechnung oder
                    Qualittsmanagement. Die Azubis bekommen auerdem
                    verantwortungsvollere Aufgaben zugeteilt und knnen jetzt
                    Untersuchungsrume selbst vorbereiten, Instrumente
                    aufbereiten oder Patienten an der Rezeption eigenstndig
                    empfangen und entlassen.
                </p>
            </div>
            <div class="example">
                <h3 class="h5">
                    3. Ausbildungsjahr
                </h3>
                <p class="mb-0">
                    Im dritten Lehrjahr kommt das groe Themenfeld der
                    Prvention auf den Unterrichtsplan. Die Azubis lernen, wie
                    Krankheiten vermieden werden und die Gesundheit gefrdert
                    wird. MFAs belgleiten in ihrem Praxisalltag den Patienten
                    oft dabei, indem sie ihn beraten und untersttzen.
                    Verantwortungsvolle Ttigkeiten am Patienten, wie das
                    Blutabnehmen oder Fden ziehen, runden die Ausbildung ab.
                </p>
            </div>
            <div class="example">
                <h3 class="h5">
                    Berichtsheft
                </h3>
                <p class="mb-0">
                    Whrend der gesamten Ausbildungszeit mssen die Azubis ein
                    Berichtsheft pflegen und ihre regelmigen Aufgaben sowie
                    das Erlernte, praktisch wie theoretisch, schriftlich
                    festhalten. Der Ausbilder, in der Regel der Praxisinhaber,
                    ist verpflichtet die angefertigten Berichte zu berwachen
                    und mit seiner Unterschrift zu besttigen. Das Berichtsheft
                    kann man also als eine Art Ausbildungsnachweis ansehen und
                    ist Voraussetzung, um die Ausbildung abschlieen zu knnen
                    und zur Abschlussprfung zugelassen zu werden.
                </p>
            </div>
            <h2>
                Zwischen- und Abschlussprfung
            </h2>
            <p>
                Am Ende des zweiten Ausbildungsjahres findet eine
                Zwischenprfung statt, um den Wissensstand der Auszubildenden
                abzufragen. Die Prfung ist schriftlich und dauert 120 Minuten.
                Dabei werden alle Themenfelder abgefragt, die die Azubis bis zu
                diesem Zeitpunkt in der Berufsschule kennengelernt haben.
            </p>
            <p>
                Um zur Abschlussprfung zugelassen zu werden, mssen die Azubis
                an der Zwischenprfung teilgenommen haben und ihr Berichtsheft
                bei der rztekammer eingereicht haben.
            </p>
            <p>
                Die Abschlussprfung wird am Ende des dritten Ausbildungsjahres
                absolviert und gliedert sich in einen schriftlichen und einen
                praktischen Prfungsteil. Die schriftlichen Klausuren umfassen
                insgesamt 300 Minuten.
            </p>
            <p>
                Bei der praktischen Prfung ziehen die Auszubildenden in der
                Regel aus mehreren Umschlgen ihre Prfungsaufgabe. Insgesamt
                dauert die praktische Prfung 75 Minuten. In dieser Zeit
                bearbeiten die Auszubildenden ihre Prfungsaufgabe und bereiten
                sich auf das Prfungsgesprch mit den Prfern vor. Die
                Auszubildenden spielen dabei eine typische Situation aus dem
                Praxisalltag nach, z.B. wie eine MFA einen chirurgischen Tisch
                vorbereitet. Danach erfolgt ein abschlieendes Fachgesprch von
                maximal 15 Minuten, indem die Auszubildenden ihr Vorgehen
                erklren und einige Fachfragen zu der Thematik beantworten.
            </p>
            <p>
                Bei erfolgreich absolvierter schriftlicher und praktischer
                Prfung mit einer Mindestnote von ausreichend ist die
                Ausbildung abgeschlossen. Die Auszubildenden erhalten einen
                Ausbildungsbrief zur/zum Medizinischen Fachangestellten.
            </p>
            <h2>
                Wie hoch ist die Ausbildungsvergtung fr Medizinische
                Fachangestellte?
            </h2>
            <p>
                Der
                <b-link
                    href="https://www.vmf-online.de/mfa/mfa-tarife"
                    target="_blank"
                    >Verband Medizinischer Fachberufe e.V. (VMF)</b-link
                >
                ist die einzige Organisation fr Medizinische Fachangestellte
                auf Arbeitnehmerseite. Sie vertritt uns Medizinische
                Fachangestellte bei Tarifverhandlungen gegenber der
                Arbeitsgemeinschaft zur Regelung der Arbeitsbedingungen der
                ArzthelferInnen / Medizinischen Fachangestellten (AAA). Die AAA
                ist die Tarifpartei der Arbeitgeber, sprich der niedergelassenen
                rzte.
            </p>
            <p>
                Der VMF setzt sich auch fr die Ausbildungsvergtung der MFA ein
                und hat bei der letzten Tarifverhandlung sehr gute Ergebnisse
                fr die Gehlter und Ausbildungsvergtungen erzielt. In den
                kommenden drei Jahren wird sich die Ausbildungsvergtung von
                Medizinischen Fachangestellten stetig erhhen. Wahnsinn, wenn
                ich daran denke, dass ich zum Start meiner Ausbildung im Jahr
                2004 gerade einmal 480 Euro verdient habe. Glcklicherweise
                konnte die Vergtung mittlerweile fast verdoppelt werden.
            </p>
            <b-table-simple
                class="my-5"
                hover
                responsive
                table-variant="secondary"
            >
                <colgroup>
                    <col />
                </colgroup>
                <colgroup>
                    <col />
                    <col />
                    <col />
                </colgroup>
                <b-thead head-variant="primary">
                    <b-tr>
                        <b-th colspan="1"> </b-th>
                        <b-th colspan="3" class="text-center">
                            Ausbildungsjahr</b-th
                        >
                    </b-tr>
                    <b-tr>
                        <b-th>ab</b-th>
                        <b-th>1.</b-th>
                        <b-th>2.</b-th>
                        <b-th>3.</b-th>
                    </b-tr>
                </b-thead>
                <b-tbody>
                    <b-tr>
                        <b-th>01.01.2021</b-th>
                        <b-td>880,- </b-td>
                        <b-td>935,- </b-td>
                        <b-td>995,- </b-td>
                    </b-tr>
                    <b-tr>
                        <b-th>01.01.2022</b-th>
                        <b-td>900,- </b-td>
                        <b-td>965,- </b-td>
                        <b-td>1.035,- </b-td>
                    </b-tr>
                    <b-tr>
                        <b-th>01.01.2023</b-th>
                        <b-td>920,- </b-td>
                        <b-td>995,- </b-td>
                        <b-td>1.075,- </b-td>
                    </b-tr>
                </b-tbody>
            </b-table-simple>
            <h2>
                Welche Mglichkeiten habe ich nach der Ausbildung als
                Medizinische Fachangestellte?
            </h2>
            <p>
                Hast du deine Ausbildung erstmal in der Tasche, wird es auch als
                ausgelernt MFA garantiert nicht langweilig. Jeder kann sich
                entsprechend seiner Fhigkeiten und Interessen auf bestimmte
                Ttigkeiten spezialisieren, z. B. als
                <b-link to="/page/mfa-career/jobs-und-berufsbilder/verwaltung"
                    >AbrechnungsmanagerIn</b-link
                >
                oder
                <b-link
                    to="/page/mfa-career/jobs-und-berufsbilder/medizinisch-technisch"
                    >MFA im ambulanten OP</b-link
                >. Manche mgen eher das Aufgabenfeld der AllrounderIn, um in
                allen Bereichen der Praxis eingesetzt zu sein.
            </p>
            <p>
                Das Wissen einer MFA ber Medizin, Therapien, Untersuchungen
                oder Abrechnung nimmt im Laufe der Berufsttigkeit stetig zu. So
                wirst du auf dem Arbeitsmarkt immer beliebt sein und keine
                Schwierigkeiten haben, eine Anstellung zu finden. Auch andere
                Arbeitgeber aus dem Gesundheitswesen schtzen die breiten
                Fhigkeiten von Medizinischen Fachangestellten. Sie versuchen
                auf sich aufmerksam zu machen durch attraktive Stellenangebote,
                die dir andere Mglichkeiten auerhalb der Arztpraxis bieten.
            </p>
            <p>
                Ganz allgemein haben wir die Kompetenzbereiche und
                Jobmglichkeiten fr Medizinische Fachangestellte /
                ArzthelferInnen in 5 Bereiche eingeteilt:
            </p>
            <b-row>
                <b-col
                    class="my-3"
                    v-for="card in cards"
                    :key="card.title"
                    cols="12"
                    md="6"
                    lg="4"
                >
