                                </p>
                            </b-card-text>
                            <b-button
                                :to="
                                    `/page/mfa-career/jobs-und-berufsbilder/${card.slug}`
                                "
                                variant="primary"
                                >Mehr</b-button
                            >
                        </b-card-body>
                    </b-card>
                </b-col>
            </b-row>
            <p>
                Jeder Bereich bietet verschiedene Mglichkeiten sich zu
                spezialisieren oder im Beruf und gehaltstechnisch aufzusteigen.
            </p>
            <p>
                Manche liebugeln eventuell mit dem Gedanken zu studieren. Hier
                gibt es einige Studiengnge, die mit entsprechender
                Berufserfahrung von MFAs absolviert werden knnen. Daneben gibt
                es verschiedene Fortbildungen oder Weiterbildungen im Sinne
                einer Aufstiegsfortbildung, die Medizinische Fachangestellte
                befhigen mehr Verantwortung zu bernehmen und in eine hhere
                Gehaltsklasse zu wechseln.
            </p>
            <p>
                Informationen ber die drei groen Bildungsformen, Fortbildung,
                Weiterbildung und Studium, und welche davon fr dich die
                Richtige ist, kannst du
                <b-link to="/article/fortbildung-weiterbildung-oder-studium"
                    >hier</b-link
                >
                nachlesen.
            </p>
            <p>
                In unserer Rubrik
                <b-link to="/page/mfa-career/fort-und-weiterbildungen"
                    >Fort- und Weiterbildung</b-link
                >
                kannst du dich informieren, welche Mglichkeiten du als
                Medizinische Fachangestellte in und auerhalb der Arztpraxis
                hast. Krankenhuser, Medizinische Versorgungszentren,
                Rehakliniken, Gesundheitsmter, Medizinische Schreibbros,
                Pflegedienste, Krankenkassen, rztekammern oder Kassenrztliche
                Vereinigungen sind dabei einige Arbeitsorte fr Medizinische
                Fachangestellte.
            </p>
            <h2>
                Wieviel verdient man nach der Ausbildung zur Medizinischen
                Fachangestellten?
            </h2>
            <p>
                Das Gehalt einer MFA orientiert sich an dem Tarifvertrag fr
                Medizinische Fachangestellte. Ausfhrliche Informationen zum
                Gehalt fr Medizinische Fachangestellte nach der Ausbildung, den
                Ttigkeitsgruppen und Einteilung der Berufsjahre findest du
                <b-link to="/page/mfa-career/gehalt">hier</b-link>.
            </p>
            <p>
                Hast du noch Fragen zum Beruf oder der Ausbildung der
                Medizinischen Fachangestellten? Dann schreib uns doch einfach
                <b-link to="/page/contact">eine Nachricht</b-link>.
            </p>
            <h2 class="h4">Quellen</h2>
            <p>
                Alle Inhalte ber die Berufsausbildung zur/zum Medizinischen
                Fachangestellten kannst du in der entsprechenden
                <b-link
                    href="https://www.bundesaerztekammer.de/fileadmin/user_upload/downloads/MFA_Verordnung_05052006.pdf"
                    >Berufsverordnung</b-link
                >
                nachlesen.
            </p>
            <ToJobboardBanner class="my-5" />
            <RandomTrainingsContainer />
        </b-container>

